@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";

#RequestNavDetails {
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.06);
    .nav-styles {
        box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.075);
    }
    
    .tab-styles {
        background-color: #F8F8F8;
    }

    .title {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: $primary;
        text-transform: capitalize;
    }

    .subtitle {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #2C2C26;
        text-transform: capitalize;
    }


    .info-tab-wrappper {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .group-wrapper {
            @include displayRow(5px);
        }
    }   
}