#RequestHistory {
    margin-top: 1rem;
    max-height: fit-content;
    .empty-history {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .custom-component-style {
        display: flex;
        align-items: center;
        justify-content: start;

        font-weight: 500;
        font-size: 14px;
        line-height: 23px;

        .color-1 {
            color: #2C2C26;
        }
        .color-2 {
            color: #3554D1;
        }
    }
}