@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";

#RequestDeteailsPage {
    .disalbed {
        border: 1px solid #999999 !important;
        background-color: #cccccc !important;
        color: #666666 !important;
    }

    .header-page {
        display: flex;
        gap: 10px;
        align-items: center;
        justify-content: space-between;
        .title-wrapper {
            display: flex;
            align-items: center;
        }
    }

    .wrapper-request-details {
        display: grid;
        grid-gap: 1.5rem;
        grid-template-columns: 3fr 1fr;

        .wrapper-info-details {
            margin-bottom: 3rem;
        }
    }

    .title-type {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: $primary;
    }

    .badge-style {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #5F5974;
        background: rgba(167, 174, 181, 0.12) !important;

        .text {
            font-weight: 600;
            font-size: 13px;
            line-height: 18px;
        }
    }

    .badge-style-1 {
        color: #3554D1 !important;
        background: rgba(53, 84, 209, 0.12) !important;
    }
    .badge-style-2 {
        color: #6D711E !important;
        background: rgba(109, 113, 30, 0.12) !important;
    }

    .request-type {
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
        color: #FF784F;

    }
    .request-type-color-1 {
        color : #6D711E !important;
    }
    .request-type-color-2 {
        color : #1B9AAA !important;
    }

    .user-name-wrapper {
        gap: 5px;
        display: flex;
        align-items: center;
        justify-content: start;

        .name {
            font-weight: 400;
            font-size: 18px;
            line-height: 23px;
            color: #5E5873;
        }
    }
}